<template>
  <div class="like">
    <van-nav-bar title="我的收藏" left-text="返回" left-arrow @click-left="back" />
    <div class="like-content clearfix" v-if="likeData.length > 0">
      <div class="content-item clearfix" v-for="(item, index) in likeData" :key="index">
        <div @click="goDetail(item.pid)" class="img fl">
          <img class="auto-img" v-lazy="item.smallImg" alt />
        </div>
        <div class="fl clearfix right">
          <div class="pro-name one-text">{{item.name}}</div>
          <div class="pro-enname one-text">{{item.enname}}</div>
          <div class="pro-price-box">
            <div class="fl pro-price">￥{{item.price}}</div>
            <div class="fr delete" @click="removeLike(item.pid, index)">
              <van-icon name="delete" />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <van-empty description="你的收藏空空如也，去逛一逛吧！" />
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //我的收藏商品数据
      likeData: []
    };
  },
  created() {
    //获取我的收藏商品数据
    this.getLikeData();
  },
  methods: {
    //返回
    back() {
      this.$router.go(-1);
    },
    //获取我的收藏商品数据
    getLikeData() {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 2000) {
            this.likeData = result.data.result;
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //根据商品id查看商品详情
    goDetail(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },
    //删除我的收藏
    removeLike(pid, index) {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/notlike",
        data: {
          appkey: this.appkey,
          tokenString,
          pid
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();
          if (result.data.code == 900) {
            this.likeData.splice(index, 1);
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    }
  }
};
</script>
<style lang="less">
.like {
  .like-content {
    margin-top: 10px;
  }
  .content-item {
    width: 90%;
    padding: 10px;
    background-color: #fff;
    margin: 0 auto;
    margin-bottom: 10px;
    border: 1px solid rgb(215, 180, 148);
    border-radius: 10px;
  }
  .img {
    width: 70px;
    img {
      width: 70px;
    }
  }
  .right {
    width: 76%;
    margin-left: 10px;
  }
  .pro-name {
    font-size: 14px;
  }
  .pro-enname {
    color: #999;
    font-size: 10px;
    margin-top: 10px;
  }
  .pro-price-box {
    width: 100%;
    margin-top: 10px;
    height: 20px;
  }
  .pro-price {
    font-size: 14px;
    color: brown;
    line-height: 20px;
  }
  .delete {
    width: 20px;
    height: 20px;
    font-size: 20px;
    color: #acacac;
  }
}
</style>